.chatHeader{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: grey;
    padding: 10px;
}

.chatHeader__hash{
    color: grey;
    font-size: 30px;
    padding:10px;
}

.chatHeader__left > h3 {
    display: flex;
    align-items: center;
    color: white;
}

.chatHeader__right{
    display: flex;
    align-items: center;
    flex: 0.5;
    justify-content: space-between;
}

.chatHeader__right > .MuiSvgIcon-root{
    cursor: pointer;
    padding:5px;
}

.chatHeader__right > .MuiSvgIcon-root :hover{
    color: white;
}

.chatHeader__search{
    display: flex;
    align-items: center;
    color: grey;
    background-color: #2f3135;
    border-radius: 3px;
    padding: 3px;
}

.chatHeader__search > input {
    background-color: transparent;
    outline-width: 0;
    color: white;
    border: none;
}